import React from 'react';
import { getMinsDiff } from "./Utils";
import {
    useLocation
} from "react-router-dom";
import NoMatch from "./NoMatch";

const config = {
    zeroDhaApiKey: "f35jttucf2u49ld9",
    Timeout: 5
}

function zerodhaPayload({ action, tradingsymbol, exchange, order_type, quantity, price, validity, dis_qty, variety }) {

    console.log(arguments);
    order_type = order_type == 'M' ? "MARKET" : order_type;
    order_type = order_type == 'L' ? "LIMIT" : order_type;

    variety = variety == 'S' ? "regular" : variety;
    validity = validity == 'D' ? "DAY" : "IOC";
    let type = action ? "BUY" : "SELL";

    return {
        "variety": variety,
        "tradingsymbol": decodeURI(tradingsymbol),
        "exchange": exchange,
        "transaction_type": type,
        "order_type": order_type,
        "quantity": parseInt(quantity),
        "readonly": false,
        "price": price ? parseFloat(price) : 0,
        "disclosed_quantity": dis_qty ? parseInt(dis_qty) : 0,
        "validity": validity
    }
}

export default function Zerodha(props) {
    let query = new URLSearchParams(useLocation().search);
    let isValid = false;
    let order = [];

    // Validate required params are exisit in qs.
    if (query.has("keys")
        && query.has("values")
        && query.has("time")) {

        let Keys = query.get("keys").split("|");
        let Values = query.get("values").split("||");
        let time = query.get("time");

        // Validate the expire time.
        if (getMinsDiff(time) < Number(config.Timeout)) {

            Values.forEach((value) => {
                let tradeObj = {}
                let trade = value.split("|");
                trade.forEach((tradeKey, indx) => {
                    console.log(indx, tradeKey, Keys[indx])
                    tradeObj[Keys[indx]] = tradeKey;
                })
                order.push(zerodhaPayload(tradeObj));
            });

            console.log(order);
            isValid = true;

        } // END IF MINs Diff
    } // END IF Qs parts length.


    let payload = JSON.stringify(order);
    let form = null;
    return (
        <>
            {isValid &&
                <>
                    <NoMatch showHome={false} subtitle="Please wait while redirecting to Zerodha ..." />
                    <form method="post" id="basket-form" action="https://kite.zerodha.com/connect/basket">
                        <input type="hidden" name="api_key" value={config.zeroDhaApiKey} />
                        <input type="hidden" id="basket" name="data" value={payload} />
                    </form>
                    {
                        setTimeout(() => {
                            document.getElementById("basket-form").submit()
                        }, 1000)
                    }
                </>}
            {!isValid &&
                <NoMatch title="Invalid Order" subtitle="Timeout 🕐" />
            }
        </>
    )
}