function renderBuySell(values, indx) {
  let final = values.values ? Object.assign({}, values, values.values) : values;
  let action = values.order[indx].action;

  if (values.values &&
    values.values.order &&
    values.values.order[indx] &&
    typeof values.values.order[indx].action != 'undefined') {
    action = values.values.order[indx].action
  }
  return (
    <>
      {action && <label className="select-none py-2 px-4 rounded-sm text-white bg-red-400" htmlFor={`values.order.${indx}.action`}>Sell</label>}
      {!action && <label className="select-none py-2 px-4 rounded-sm text-white bg-green-400" htmlFor={`values.order.${indx}.action`}>Buy</label>}
    </>
  )
}

function ToggleExtra() {
  let $rows = document.getElementById("rows")
  for (let ridx = 0; ridx < $rows.children.length; ridx++) {
    var $tr = $rows.children[ridx];
    if (!$tr.children[0].children[0].checked) {
      $tr.classList.toggle("hidden")
    }
  }

  document.querySelectorAll("#orderTable td:nth-child(1),th:nth-child(1)").forEach((td) => td.classList.toggle("hidden"))

  document.querySelectorAll("#orderTable td:last-child,th:last-child").forEach((td) => td.classList.toggle("hidden"))

}

